import { Component, OnInit } from '@angular/core';
import { LoadingController, NavController } from '@ionic/angular';
import { catchError } from 'rxjs/operators';
import { ICity, IProvince } from 'src/app/models/country.model';
import { IRequestResponse } from 'src/app/models/response.model';
import { IUsers } from 'src/app/models/users.model';
import { AbmService } from 'src/app/services/abm.service';
import { ToolsService } from 'src/app/services/tools.service';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SubjectsService } from 'src/app/services/subjects';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
  provinces: IProvince[] = [];
  cities: ICity[] = [];
  profileForm: FormGroup;
  user: IUsers;

  constructor(
    private navCtrl: NavController,
    private abmService: AbmService,
    private loadingCtrl: LoadingController,
    private tools: ToolsService,
    private fb: FormBuilder,
    private subjects: SubjectsService
  ) {
    this.profileForm = this.fb.group({
      id_user: ['', Validators.required],
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      full_name: ['', Validators.required],
      cellphone: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      pass: [''],
      birthdate: ['', Validators.required],
      province: ['', Validators.required],
      city: ['', Validators.required],
      expose_phone: [true],
      expose_email: [true],
      province_id: [-1, Validators.required],
      city_id: [-2, Validators.required],
    });
  }
  ngOnInit(): void {
    this.subjects.user$.subscribe((user) => {
      this.user = user;
      if (user) {
        this.setDataUser();
      }
    });
  }

  loadProvinces() {
    this.subjects.provinces$.subscribe((data: IProvince[]) => {
      this.provinces = [...data];
      setTimeout(() => {
        this.profileForm.get('province_id').setValue(this.user.province_id);
        this.loadCities();
      }, 50);
    });
  }

  loadCities() {
    if (this.provinces.length > 0) {
      const prov: IProvince = this.provinces.find(
        (x) => x.geonameId === this.profileForm.get('province_id').value
      );
      this.cities = [...prov.cities];
      setTimeout(() => {
        this.profileForm.get('city_id').setValue(this.user.city_id);
        this.profileForm.get('city_id').markAsTouched();
      }, 500);
    }
  }

  get isValidName() {
    let firstName = this.tools.capitalizeAllWords(
      this.profileForm.get('first_name').value
    );
    this.profileForm.get('first_name').setValue(firstName);
    return (
      this.profileForm.get('first_name').value.length > 0 ||
      !this.profileForm.get('first_name').touched
    );
  }
  get isValidLastName() {
    let last_name = this.tools.capitalizeAllWords(
      this.profileForm.get('last_name').value
    );
    this.profileForm.get('last_name').setValue(last_name);
    return (
      this.profileForm.get('last_name').value.length > 0 ||
      !this.profileForm.get('last_name').touched
    );
  }
  get isValidPhone() {
    let cellphone = this.tools.validatePhoneNumber(
      this.profileForm.get('cellphone').value
    );
    this.profileForm.get('cellphone').setValue(cellphone);
    return (
      this.profileForm.get('cellphone').value.length >= 10 ||
      !this.profileForm.get('cellphone').touched
    );
  }
  get isValidEmail() {
    return (
      (this.profileForm.value.email.length > 0 &&
        this.tools.validEmail(this.profileForm.value.email)) ||
      !this.profileForm.get('email').touched
    );
  }
  get isValidPass() {
    return (
      this.profileForm.get('pass').value.length > 3 ||
      this.profileForm.get('pass').value.length === 0
    );
  }
  get isValidBirthdate() {
    return (
      this.profileForm.get('birthdate').value.length === 10 ||
      !this.profileForm.get('birthdate').touched
    );
  }

  get isValidProvince() {
    return (
      this.profileForm.get('province_id').value > -2 ||
      !this.profileForm.get('province_id').touched
    );
  }
  get isValidCity() {
    return (
      this.profileForm.get('city_id').value > -2 ||
      !this.profileForm.get('city_id').touched
    );
  }

  get ValidForm() {
    return (
      this.isValidName &&
      this.isValidLastName &&
      this.isValidPhone &&
      this.isValidEmail &&
      this.isValidPass &&
      this.isValidBirthdate &&
      this.isValidProvince &&
      this.isValidCity
    );
  }

  async sendData() {
    let type = 'update';
    let mjs = 'actualizar';
    Object.keys(this.profileForm.controls).forEach((key) => {
      this.profileForm.get(key).markAsTouched();
    });
    if (!this.ValidForm) {
      Swal.fire({
        icon: 'warning',
        title: 'Campos incompletos',
      });
      return;
    }
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 0,
      cssClass: 'custom-loading',
    });

    loading.present();
    this.profileForm
      .get('province')
      .setValue(
        this.tools.getNameListCountries(
          [...this.provinces],
          this.profileForm.get('province_id').value,
          'toponymName'
        )
      );
    this.profileForm
      .get('city')
      .setValue(
        this.tools.getNameListCountries(
          [...this.cities],
          this.profileForm.get('city_id').value,
          'toponymName'
        )
      );
    let form = new FormData();
    form.append('id_user', this.profileForm.get('id_user').value);
    form.append('first_name', this.profileForm.get('first_name').value);
    form.append('last_name', this.profileForm.get('last_name').value);
    form.append(
      'full_name',
      this.profileForm.get('first_name').value +
        ' ' +
        this.profileForm.get('last_name').value
    );
    form.append('birthdate', this.profileForm.get('birthdate').value);
    form.append('cellphone', this.profileForm.get('cellphone').value);
    form.append('email', this.profileForm.get('email').value);
    form.append('pass', this.profileForm.get('pass').value);
    form.append('province', this.profileForm.get('province').value);
    form.append('city', this.profileForm.get('city').value);
    form.append(
      'province_id',
      this.profileForm.get('province_id').value.toString()
    );
    form.append('city_id', this.profileForm.get('city_id').value.toString());
    form.append(
      'expose_phone',
      this.profileForm.get('expose_phone').value.toString()
    );
    form.append(
      'expose_email',
      this.profileForm.get('expose_email').value.toString()
    );
    form.append('type', type);
    this.abmService
      .ABMUsers(form)
      .pipe(
        catchError((error) => {
          Swal.fire({
            icon: 'error',
            title: 'Error del servidor',
          });
          loading.dismiss();
          throw { ...error };
        })
      )
      .subscribe((response: IRequestResponse<IUsers>) => {
        loading.dismiss();
        if (response.result) {
          Swal.fire({
            icon: 'success',
            title: 'Usuario registrado',
          });
          const sessionEncrypted = btoa(JSON.stringify(response.data));
          localStorage.setItem('token', sessionEncrypted);
          return;
        }
        Swal.fire({
          icon: 'error',
          title: 'Error al ' + mjs + ' usuario',
          text: response.message,
        });
      });
  }

  setDataUser() {
    this.profileForm.get('id_user').setValue(this.user.id_user);
    this.profileForm.get('first_name').setValue(this.user.first_name);
    this.profileForm.get('last_name').setValue(this.user.last_name);
    this.profileForm.get('full_name').setValue(this.user.full_name);
    this.profileForm.get('cellphone').setValue(this.user.cellphone);
    this.profileForm.get('email').setValue(this.user.email);
    this.profileForm.get('birthdate').setValue(this.user.birthdate);
    this.profileForm.get('province').setValue(this.user.province);
    this.profileForm.get('city').setValue(this.user.city);
    this.profileForm.get('expose_phone').setValue(this.user.expose_phone);
    this.profileForm.get('expose_email').setValue(this.user.expose_email);

    this.loadProvinces();
  }
}
